const router=require("express").Router();
const BlogController=require("../controllers/BlogController");
const verifyToken =require("../middlewares/verifyToken");
const isAdmin=require("../middlewares/isAdmin");
const BlogValidation=require("../middlewares/BlogValidation");
const upload=require("../utils/multer")
router.post("/",BlogValidation,verifyToken,isAdmin,upload.single("image"),BlogController.AddBlog);
router.delete("/:id",verifyToken,isAdmin,BlogController.deletBlog);
router.get("/",verifyToken,isAdmin,BlogController.findData);
router.put("/:id",verifyToken,isAdmin,upload.single("image"),BlogController.updateBlog);
router.get("/:id",verifyToken,isAdmin,BlogController.FindById);
module.exports =router;